import * as React from "react";
import Breadcrumb from 'antd/lib/breadcrumb';import 'antd/lib/breadcrumb/style/css.js';import Layout from 'antd/lib/layout';import 'antd/lib/layout/style/css.js';

const { Content } = Layout;

class Main extends React.Component {
  constructor(props: any) {
    super(props);
  }

  public render() {
    return (
      <Layout style={{ padding: "0 24px 24px" }}>
         <Breadcrumb style={{ margin: '16px 0' }}/>
          <Content
            style={{
              margin: 0,
              minHeight: 280
            }}
          >
            {this.props.children}
          </Content>
      </Layout>
    );
  }

}

export default Main



